import React, { useRef, useEffect } from 'react'
import { useInView } from 'react-intersection-observer'
import './AboutSection.scss'
import Rellax from 'rellax'
import gsap from 'gsap'

function AboutSection({ img }) {
  let image = useRef(null)
  let imageContainer = useRef(null)

  const [ref, inView] = useInView({ triggerOnce: false, threshold: 0.5 })

  useEffect(() => {
    if (inView) {
      let tl = gsap.timeline()
      tl.to('#path22', {
        css: {
          fill: 'black',
        },
        delay: 0,
        duration: 0.25,
        ease: 'power1.in',
      })
    }
  }, [inView])

  useEffect(() => {
    new Rellax(imageContainer, {
      speed: 0,
      center: false,
      wrapper: null,
      round: true,
      vertical: true,
      horizontal: false,
    })
    new Rellax(image, {
      speed: -0.75,
      center: false,
      wrapper: null,
      round: true,
      vertical: true,
      horizontal: false,
    })
  }, [image, imageContainer])

  return (
    <section id="about" ref={ref}>
      <div className="about__wrapper">
        <div
          className="about__image-container"
          ref={el => (imageContainer = el)}
        >
          <div
            className="about__image"
            ref={el => (image = el)}
            style={{ backgroundImage: `url(${img.src})` }}
          ></div>
        </div>
        <div
          className="about__content"
          data-aos="fade"
          data-aos-delay="50"
          data-aos-duration="1000"
          data-aos-easing="ease-in-out"
          data-aos-once="false"
          data-aos-anchor-placement="top"
        >
          <h2 className="about__title">О нас</h2>
          <div className="about__text">
            <p>
              Авторская кухня. Специализируемся на блюдах, приготовленных на
              гриле. Используем печь{' '}
              <a
                href="https://www.josper.es/ru/josper/about/"
                target="_blank"
                rel="noopener noreferrer"
                title="О печах Josper"
              >
                Josper.
              </a>
            </p>
            <p>
              Классические и фирменные коктейли. <br />
              Хороший выбор вина в правильных бокалах.
            </p>
            <p>
              Вы найдете нас на территории торгово-делового квартала&nbsp;
              <a
                className="index__link"
                href="http://likerka-loft.ru/"
                target="_blank"
                rel="noopener noreferrer"
                title="Сайт likerka-loft.ru"
              >
                &laquo;Ликерка Лофт&raquo;
              </a>{' '}
              по адресу: проспект Ленина, дом 85, корпус 5, вход 1.
            </p>
          </div>
        </div>
      </div>
    </section>
  )
}

export default AboutSection
